import { Spin } from "antd";
import "./EditCanvas.scss";
import { useGetComponentInfo } from "../../../hooks/useGetComponentInfo";
import {
  ComponentInfoType,
  changeSelectedId,
  moveComponent,
} from "../../../store/componentsReducer";
import { getComponentConfByType } from "../../../components/QuestionComponents";
import { useDispatch } from "react-redux";
import classNames from "classnames";
import { MouseEvent } from "react";
import { useBindCanvasKeyPress } from "../../../hooks/useBindCanvasKeyPress";
import SortableContainer from "../../../components/DragSortable/SortableContainer";
import SortableItem from "../../../components/DragSortable/SortableItem";
type EditCanvasPropsType = {
  loading: boolean;
};
function genComponent(componentInfo: ComponentInfoType) {
  const { type, props } = componentInfo;
  const componentConf = getComponentConfByType(type);
  if (componentConf == null) return null;
  const { Component } = componentConf;
  return <Component {...props}></Component>;
}
const EditCanvas = (props: EditCanvasPropsType) => {
  useBindCanvasKeyPress();
  const { loading } = props;
  const { componentList, selectedId } = useGetComponentInfo();
  const dispatch = useDispatch();
  function handleClick(event: MouseEvent, id: string) {
    event.stopPropagation();
    dispatch(changeSelectedId(id));
  }
  if (loading) {
    return (
      <div className="spinStyle">
        <Spin></Spin>
      </div>
    );
  }
  const componentListWithId = componentList.map((c) => {
    return {
      ...c,
      id: c.fe_id,
    };
  });
  function handleDragEnd(oldIndex: number, newIndex: number) {
    dispatch(moveComponent({ oldIndex, newIndex }));
  }
  return (
    <SortableContainer items={componentListWithId} onDragEnd={handleDragEnd}>
      <div className="edit_canvas">
        {componentList
          .filter((v) => !v.isHidden)
          .map((c) => {
            const { fe_id } = c;
            const classes = classNames("component_wrapper", {
              selected: selectedId === fe_id,
              locked: c.isLocked,
            });
            return (
              <SortableItem key={fe_id} id={fe_id}>
                <div className={classes} onClick={(e) => handleClick(e, fe_id)}>
                  <div className="component">{genComponent(c)}</div>
                </div>
              </SortableItem>
            );
          })}
        {/* <div className="component_wrapper">
      <div className="component">
        <QuestionTitle></QuestionTitle>
      </div>
    </div>
    <div className="component_wrapper">
      <div className="component">
        <QuestionInput></QuestionInput>
      </div>
    </div> */}
      </div>
    </SortableContainer>
  );
};
export default EditCanvas;
