import React, { useEffect, useState } from "react";
import { FileTextOutlined, SettingOutlined } from "@ant-design/icons";
import { Tabs } from "antd";
import ComponentProp from "./ComponentProp";
import PageSetting from "./PageSetting";
import { useGetComponentInfo } from "../../../hooks/useGetComponentInfo";
enum TabsKey {
  prop = "prop",
  setting = "setting",
}
const RightPanel: React.FC = () => {
  const [activeKey, setActiveKey] = useState(TabsKey.prop);
  const { selectedId } = useGetComponentInfo();
  useEffect(() => {
    if (selectedId) setActiveKey(TabsKey.prop);
    else setActiveKey(TabsKey.setting);
  }, [selectedId]);
  const tabsItems = [
    {
      key: TabsKey.prop,
      label: (
        <span>
          <FileTextOutlined />
          属性
        </span>
      ),
      children: <ComponentProp></ComponentProp>,
    },
    {
      key: TabsKey.setting,
      label: (
        <span>
          <SettingOutlined />
          页面设置
        </span>
      ),
      children: <PageSetting></PageSetting>,
    },
  ];
  return <Tabs activeKey={activeKey} items={tabsItems} />;
};

export default RightPanel;
