import { useDispatch } from "react-redux";
import QuestionInput from "../../../components/QuestionComponents/QuestionInput/QuestionInput";
import QuestionTitle from "../../../components/QuestionComponents/QuestionTitle/QuestionTitle";
import { useLoadQuestionData } from "../../../hooks/useLoadQuestionData";
import "./Edit.scss";
import EditCanvas from "./EditCanvas";
import { changeSelectedId } from "../../../store/componentsReducer";
import LeftPanel from "./LeftPanel";
import RightPanel from "./RightPanel";
import EditHeader from "./EditHeader";
const Edit = () => {
  const { loading } = useLoadQuestionData();
  const dispatch = useDispatch();
  function clearSelectedId() {
    dispatch(changeSelectedId(""));
  }
  return (
    <div className="edit_container">
      <div className="header">
        <EditHeader></EditHeader>
      </div>
      <div className="content_wrapper">
        <div className="content">
          <div className="left">
            <LeftPanel></LeftPanel>
          </div>
          <div className="main" onClick={clearSelectedId}>
            <div className="canvas_wrapper">
              <EditCanvas loading={loading}></EditCanvas>
            </div>
          </div>
          <div className="right">
            <RightPanel></RightPanel>
          </div>
        </div>
      </div>
    </div>
  );
};
export default Edit;
