import Title from "antd/es/typography/Title";
import {
  ComponentConfType,
  componentConfGroup,
} from "../../../components/QuestionComponents";
import "./ComponentLib.scss";
import { useDispatch } from "react-redux";
import { addComponent } from "../../../store/componentsReducer";

function GetComponent(c: ComponentConfType) {
  const { title, type, Component, defaultProps } = c;
  const dispatch = useDispatch();
  function handleClick() {
    dispatch(
      addComponent({
        fe_id: Math.random().toString(36).substr(2),
        type,
        title,
        props: defaultProps,
      })
    );
  }
  return (
    <div key={type} className="componentLib_wrapper" onClick={handleClick}>
      <div className="component">
        <Component></Component>
      </div>
    </div>
  );
}
const ComponentLib = () => {
  return (
    <>
      {componentConfGroup.map((group, index) => {
        const { groupId, groupName, components } = group;
        return (
          <div key={groupId}>
            <Title
              level={3}
              style={{ fontSize: "16px", marginTop: index > 0 ? "20px" : "0" }}
            >
              {groupName}
            </Title>
            <div className="libComStyle">{components.map((c) => GetComponent(c))}</div>
          </div>
        );
      })}
    </>
  );
};

export default ComponentLib;
